@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: "PT Serif", serif;
  font-size: 1rem;
  color: #333333;
}

img {
  max-width: 100%;
}

a {
  color: #333333;
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 18px;
}

.site-title {
  width: 110px;
  line-height: 1px;
  margin-right: 50px;
}
.site-title a {
  display: block;
}

.text-title {
  font-size: 1.25rem;
  font-weight: 400;
  margin-bottom: 5px;
}
@media (max-width: 767px) {
  .text-title {
    font-size: 1.25rem;
  }
}

.sub-title {
  display: inline-block;
  margin-bottom: 20px;
  font-size: 0.875rem;
}

/*-------------------------------------------
mainvisual
-------------------------------------------*/
#mainvisual {
  width: 90vw;
  margin: 4% auto 0 auto;
}
@media (max-width: 767px) {
  #mainvisual {
    width: 100%;
    margin: 0;
  }
}
@media (max-width: 767px) {
  #mainvisual img {
    width: 100%;
    height: 50vh;
    object-fit: cover;
  }
}

/*-------------------------------------------
header
-------------------------------------------*/
#header {
  width: 90vw;
  display: flex;
  align-items: center;
  margin: 0 auto 30px auto;
  padding: 32px 0;
}
@media (max-width: 767px) {
  #header {
    width: 100%;
    padding: 20px 18px;
  }
}
#header .navigation ul {
  display: flex;
}
#header .navigation ul li {
  margin-right: 30px;
  font-size: 0.875rem;
}
#header .navigation ul li:last-child {
  margin-right: 0;
}

/*-------------------------------------------
concept, work
-------------------------------------------*/
.content {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
}
@media (max-width: 767px) {
  .content {
    flex-direction: column;
  }
}
.content .image {
  width: 50%;
}
@media (max-width: 767px) {
  .content .image {
    width: 100%;
    margin-bottom: 10px;
  }
}
.content .text {
  width: 50%;
  padding: 0 7%;
}
@media (max-width: 767px) {
  .content .text {
    width: 100%;
    padding: 0;
  }
}

@media (max-width: 767px) {
  #work {
    flex-direction: column-reverse;
  }
}

/*-------------------------------------------
footer
-------------------------------------------*/
#footer {
  display: flex;
  justify-content: space-between;
  font-size: 0.75rem;
  padding: 20px 0;
}
#footer .logo {
  width: 110px;
}